import React, { useState } from 'react'
import Board from './Board'

const Game = () => {

    // At start, history is one element array, with element being an array
    // representing 9 square game board
    const [history, setHistory] = useState([{ squares: Array(9).fill(null) }])
    const [boardNumber, setBoardNumber] = useState(0)
    const [xIsNext, setXIsNext] = useState(true)

    const calculateWinner = squares => {
        const lines = [
            [0, 1, 2],
            [3, 4, 5],
            [6, 7, 8],
            [0, 3, 6],
            [1, 4, 7],
            [2, 5, 8],
            [0, 4, 8],
            [2, 4, 6]
        ]

        for (let i = 0; i < lines.length; i++) {
            const [a, b, c] = lines[i]
            if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
                return squares[a]
            }
        }
        return null
    }
    
    const handleClick = i => {
        const _history = history.slice(0, boardNumber + 1)
        const _currentBoard = _history[_history.length -1]
        const _squares = _currentBoard.squares.slice()

        // Check for someone already won or square already taken
        if (calculateWinner(_squares) || _squares[i]) { return }
        
        _squares[i] = xIsNext ? 'X' : 'O'
        setHistory(_history.concat([{ squares: _squares }]))
        setBoardNumber(_history.length)
        setXIsNext(!xIsNext)
    }

    const jumpTo = boardIndex => {
        setBoardNumber(boardIndex)
        setXIsNext((boardIndex % 2) === 0)
    }

    const moveHistory = history.map((move, index) => {
        const desc = index ? `Go to move #${index}` : 'Go to game start'
        return (
            <li key={index}>
                <button onClick={() => jumpTo(index)}>{desc}</button>
            </li>
        )
    })

    const currentBoard = history[boardNumber]
    const winner = calculateWinner(currentBoard.squares)

    let status
    if (winner) {
        status = `Winner: ${winner}`
    } else if (boardNumber === 9) {
        status = `It's a draw!`
    } else {
        status = `Next player: ${xIsNext ? 'X' : 'O'}`
    }

    return (
        
        <div className="game">

            <div className="game-board">
                <Board
                    squares={currentBoard.squares}
                    onClick={i => handleClick(i)}
                />
            </div>

            <div className="game-info">
                <div className="mb-4"><strong>{status}</strong></div>
                <ol>{moveHistory}</ol>
            </div>

        </div>
        
    )
}

export default Game
